//we make a common page from home n about than we pass props
import React from 'react';
import { NavLink } from 'react-router-dom';

const Common = (props) => {
	return (
		<>
			<section id='header' className='d-flex align-items-center'>
				<div className='container-fluid '>
					<div className='row '>
						<div className='col-10 mx-auto'>
							<div className='row'>
								<div className='col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column'>
									<h1>
										{props.name}
										<strong className='brand_name ml-3'>Shatakshya</strong>
									</h1>
									<p className='my-3'>
										We are the Team of talented developer for making Websites
									</p>
									<div className='mt-3'>
										<NavLink to={props.visit} className='btn btn-outline-info'>
											{props.btname}
										</NavLink>
									</div>
								</div>

								{/* for images */}
								<div className='col-lg-6 order-1 order-lg-2 '>
									<img
										src={props.imgsrc}
										className='img-fluid animated'
										alt='homeimg'
									/>
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>
		</>
	);
};

export default Common;
